import React, { Component } from 'react';

import './App.css';
import KnowHow from './KnowHow';
import Results from './Results';
import KnowToBe from './KnowToBe';

class App extends Component {
  state = {
    clicked: 0,
    category1: 0,
    category2: 0,
    category3: 0,
    category4: 0,
    acquis: 0,
    enCoursDacquisition: 0,
    nonAcquis: 0,
    nonEvalue: 0,
    knowtobeCat1: 0,
    knowtobeCat2: 0,
    knowtobeCat3: 0,
    knowtobeCat4: 0
  }

  closeResults = () => {
    this.setState({ 
      clicked: 0,
      category1: 0,
      category2: 0,
      category3: 0,
      category4: 0,
      knowtobeCat1: 0,
      knowtobeCat2: 0,
      knowtobeCat3: 0,
      knowtobeCat4: 0,
      acquis: 0,
      enCoursDacquisition: 0,
      nonAcquis: 0,
      nonEvalue: 0
    });
  }

  radioHandlerKnowToBe = () => {
    const radioList = document.getElementsByClassName('radio');
    for(let i = 0; i < radioList.length; i++) {
      const radioListChecked = radioList[i].checked;
      if(radioListChecked) {
        switch(radioList[i].value) {
          case '1':
            this.setState(prevState => {
              return {knowtobeCat1: prevState.knowtobeCat1 + 1}
            });
            break;
            case '2':
            this.setState(prevState => {
              return {knowtobeCat2: prevState.knowtobeCat2 + 1}
            });
            break;
            case '3':
            this.setState(prevState => {
              return {knowtobeCat3: prevState.knowtobeCat3 + 1}
            });
            break;
            case '4':
            this.setState(prevState => {
              return {knowtobeCat4: prevState.knowtobeCat4 + 1}
            });
            break;
            default:
            continue;
        }
      }
    }
    this.setState({ clicked : 2 });
  }

  radioHandler = () => {
    const radioList = document.getElementsByClassName(`radio`);
      for(let i = 0; i < radioList.length; i++) {
        const radioListChecked = radioList[i].checked;
        if(radioListChecked) {
          switch(radioList[i].value) {
            case '1':
            this.setState(prevState => {
              return {category1: prevState.category1 + 1, acquis: prevState.acquis + 1}
            });
            break;
            case '2':
            this.setState(prevState => {
              return {category2: prevState.category2 + 1, acquis: prevState.acquis + 1}
            });
            break;
            case '3':
            this.setState(prevState => {
              return {category3: prevState.category3 + 1, acquis: prevState.acquis + 1}
            });
            break;
            case '4':
            this.setState(prevState => {
              return {category4: prevState.category4 + 1, acquis: prevState.acquis + 1}
            });
            break;
            case 'EC':
            this.setState(prevState => {
              return {enCoursDacquisition: prevState.enCoursDacquisition + 1}
            });
            break;
            case 'NA':
            this.setState(prevState => {
              return {nonAcquis: prevState.nonAcquis + 1}
            });
            break;
            case 'NE':
            this.setState(prevState => {
              return {nonEvalue: prevState.nonEvalue + 1}
            });
            break;
            default:
            continue;
        }
      }
    }
    this.setState({ clicked : 1 });
}

renderApp = () => {
  const clicked = this.state.clicked;
  switch(clicked) {
    case 0:
    return <KnowHow
              radioHandler={this.radioHandler}
          />
    case 1:
    return <KnowToBe
              radioHandler={this.radioHandlerKnowToBe}
          />
    case 2:
    return <Results
            cat1={this.state.category1}
            cat2={this.state.category2}
            cat3={this.state.category3}
            cat4={this.state.category4}
            knowtobecat1={this.state.knowtobeCat1}
            knowtobecat2={this.state.knowtobeCat2}
            knowtobecat3={this.state.knowtobeCat3}
            knowtobecat4={this.state.knowtobeCat4}
            acquis={this.state.acquis}
            enCours={this.state.enCoursDacquisition}
            nonAcquis={this.state.nonAcquis}
            nonEvalue={this.state.nonEvalue}
            closeResults={this.closeResults}
            categoryGotIt={this.state.categoryGotIt}
          />
    default:
    return null
  }
}

  render() {
    return (
      <div className="App">
        {this.renderApp()}
      </div>
    );
  }
}

export default App;
