import React, { Component } from 'react';
import Rows from './Rows';
import data from './data';

class KnowHow extends Component {
    state = {
        header: ['Titre', 'Critères observables', 'Catégorie', 'Acquis', 'En cours', 'Non acquis', 'Non évalué'],
    }

    header = (
        this.state.header.map(
          (item, id) => <div key={id}>{item}</div>
        )
      );

    render() { 
        return ( 
        <section>
          <header className='App-titres'>
            {this.header}
          </header>
          <article>
            <Rows
              row={data}
            />
            <button type='button' id='valider' onClick={e => this.props.radioHandler(e)}>Valider</button>
          </article>
        </section>
         );
    }
}
 
export default KnowHow;