import React, { Component } from 'react';
import dataKnowToBe from './dataKnowToBe';

class KnowToBE extends Component {
        state = { 
            header: ['Critères', 'Non acquis', 'En cours d\'acquisition', 'Acquis', 'Acquis ++']
         }

    header = (
        this.state.header.map(
          (item, id) => <div key={id}>{item}</div>
        )
      );

    render() { 
        const data = dataKnowToBe;
        return ( 
                <section>
                    <header className='App-titres-knowtobe'>
                        {this.header}
                    </header>
                <article className='article-knowtobe'>
                    {
                        data.map(
                            item => 
                            <div key={item.id} className='App-rows-knowtobe'>
                                <div>{item.titre}</div>
                                    <label htmlFor={`${item.id}-1`}>
                                        <input type='radio' id={`${item.id}-1`} className={`radio`} name={item.id} value={1}/>
                                    </label>
                                    <label htmlFor={`${item.id}-2`}>
                                        <input type='radio' id={`${item.id}-2`} className={`radio`} name={item.id} value={2}/>
                                    </label>
                                    <label htmlFor={`${item.id}-3`}>
                                        <input type='radio' id={`${item.id}-3`} className={`radio`} name={item.id} value={3}/>
                                    </label>
                                    <label htmlFor={`${item.id}-4`}>
                                        <input type='radio' id={`${item.id}-4`} className={`radio`} name={item.id} value={4}/>
                                    </label>
                            </div>
                            
                        )
                    }
                    <button type='button' id='valider' onClick={this.props.radioHandler}>Valider</button>
                </article>
                
                </section>
         );
    }
}
 
export default KnowToBE;