import React, { Component } from 'react';

class Rows extends Component {
    render() {
        const row = this.props.row;
        return (
            <>
                {
                    row.map(
                        item => 
                        <div key={item.titreCategory.length} className='category'>
                            <h3 className='App-rows-title'>{item.titreCategory}</h3>
                            {
                                item.sousCategory.map(elt =>
                                    <div className='App-rows' key={elt.id}>
                                        <div>{elt.titre}</div>
                                        <div>{elt.critere}</div>
                                        <div>{elt.categorie}</div>
                                        <label htmlFor={`${elt.id}-1`}>
                                            <input type='radio' id={`${elt.id}-1`} className={`radio`} name={elt.id} value={elt.categorie} />
                                        </label>
                                        <label htmlFor={`${elt.id}-2`}>
                                            <input type='radio' id={`${elt.id}-2`} className={`radio`} name={elt.id} value='EC'/>
                                        </label>
                                        <label htmlFor={`${elt.id}-3`}>
                                            <input type='radio' id={`${elt.id}-3`} className={`radio`} name={elt.id} value='NA'/>
                                        </label>
                                        <label htmlFor={`${elt.id}-4`}>
                                            <input type='radio' id={`${elt.id}-4`} className={`radio`} name={elt.id} value='NE'/>
                                        </label>
                                    </div>
                                    )
                            }
                        </div>
                    )
                }
            </>
        )
    }
}
 
export default Rows;