import React, { Component } from 'react';

class Results extends Component {
    state = {
        categoryHow: '',
        categoryToBe: '',
        savoirFaire: 0,
        savoirEtre: 0,
        catRetenue: 0,
        percentSavoirFaire: 0,
        percentSavoirEtre: 0,
        totalPercent: 0,
        moyenneCat1: Math.round(this.props.cat1/29*30),
        moyenneCat2: Math.round(this.props.cat2/33*30),
        moyenneCat3: Math.round(this.props.cat3/38*30),
        moyenneCat4: Math.round(this.props.cat4/20*30),
        styleSF1: '#1DA598',
        styleSF2: '#1DA598',
        styleSF3: '#1DA598',
        styleSF4: '#1DA598',
    }

    componentWillMount() {
        this.getCategoryHow();
        this.getCategoryToBe();
    }

    componentDidMount() {
        this.getFinalCategory();
        this.getStyle();
    }

    getFinalCategory = () => {
        const savoirFaire = this.state.savoirFaire;
        const savoirEtre = this.state.savoirEtre;
        if(savoirFaire === 1 && savoirEtre === 1 ) {
            this.setState({ catRetenue: 1 });
        } else if(savoirFaire === 1 && savoirEtre === 2) {
            this.setState({ catRetenue: 1 });
        } else if(savoirFaire === 1 && savoirEtre === 3) {
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 1 && savoirEtre === 4){
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 2 && savoirEtre === 1) {
            this.setState({ catRetenue: 1 });
        } else if(savoirFaire === 2 && savoirEtre === 2) {
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 2 && savoirEtre === 3){
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 2 && savoirEtre === 4 ) {
            this.setState({ catRetenue: 3 });
        } else if(savoirFaire === 3 && savoirEtre === 1) {
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 3 && savoirEtre === 2){
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 3 && savoirEtre === 3) {
            this.setState({ catRetenue: 3 });
        } else if(savoirFaire === 3 && savoirEtre === 4){
            this.setState({ catRetenue: 3 });
        } else if(savoirFaire === 4 && savoirEtre === 1 ) {
            this.setState({ catRetenue: 2 });
        } else if(savoirFaire === 4 && savoirEtre === 2) {
            this.setState({ catRetenue: 3 });
        } else if(savoirFaire === 4 && savoirEtre === 3){
            this.setState({ catRetenue: 3 });
        } else if(savoirFaire === 4 && savoirEtre === 4 ) {
            this.setState({ catRetenue: 4 });
        } else {
            this.setState({ catRetenue: 5 });
        }
        this.setState({
            totalPercent: (this.state.percentSavoirEtre + this.state.percentSavoirFaire) / 2
        });
    }

    getCategoryToBe = () => {
        const cat1 = this.props.knowtobecat1;
        const cat2 = this.props.knowtobecat2;
        const cat3 = this.props.knowtobecat3 ;
        const cat4 = this.props.knowtobecat4;
        const myArray = [cat1, cat2, cat3, cat4];
        myArray.sort((a, b) => b - a);
        const myNumber = myArray[0];
        switch(myNumber) {
        case cat1:
        this.setState({ categoryToBe: 'catégorie 1', 
            savoirEtre: 1, 
            percentSavoirEtre: Math.round(myNumber/15*100) 
        });
        break;
        case cat2:
        this.setState({ categoryToBe: 'catégorie 2', 
            savoirEtre: 2, 
            percentSavoirEtre: Math.round(myNumber/15*100) 
        });
        break;
        case cat3:
        this.setState({ categoryToBe: 'catégorie 3', 
            savoirEtre: 3, 
            percentSavoirEtre: Math.round(myNumber/15*100) });
        break;
        case cat4:
        this.setState({ categoryToBe: 'catégorie 4', 
            savoirEtre: 4, 
            percentSavoirEtre: Math.round(myNumber/15*100) });
        break;
        default: 
        this.setState({ categoryToBe: 'catégorie inconnue' });
        }
    }

    getCategoryHow = () => {
        const cat1 = this.state.moyenneCat1;
        const cat2 = this.state.moyenneCat1;
        const cat3 = this.state.moyenneCat1;
        const cat4 = this.state.moyenneCat1;
        const myArray = [cat1, cat2, cat3, cat4];
        myArray.sort((a, b) => b - a);
        const myNumber = myArray[0];
        switch(myNumber) {
        case cat1:
        this.setState({ categoryHow: 'catégorie 1', 
            savoirFaire: 1, 
            percentSavoirFaire: Math.round(myNumber/30*100)
        });
        break;
        case cat2:
        this.setState({ categoryHow: 'catégorie 2', 
            savoirFaire: 2, 
            percentSavoirFaire: Math.round(myNumber/30*100)
        });
        break;
        case cat3:
        this.setState({ categoryHow: 'catégorie 3', 
            savoirFaire: 3, 
            percentSavoirFaire: Math.round(myNumber/30*100)
        });
        break;
        case cat4:
        this.setState({ categoryHow: 'catégorie 4', 
            savoirFaire: 4, 
            percentSavoirFaire: Math.round(myNumber/30*100)
        });
        break;
        default: 
        this.setState({ categoryHow: 'catégorie inconnue' });
        }
    }

    getStyle = () => {
        const myArray = [this.state.moyenneCat1, this.state.moyenneCat2, this.state.moyenneCat3, this.state.moyenneCat4];
            myArray.sort((a, b) => b - a);
            const myNumber = myArray[0];
        switch(myNumber) {
            case this.state.moyenneCat1:
            this.setState({ styleSF1: 'rgb(178, 128, 201)'});
            break;
            case this.state.moyenneCat2:
            this.setState({ styleSF2: 'rgb(178, 128, 201)'});
            break;
            case this.state.moyenneCat3:
            this.setState({ styleSF3: 'rgb(178, 128, 201)'});
            break;
            case this.state.moyenneCat4:
            this.setState({ styleSF4: 'rgb(178, 128, 201)'});
            break;
            default:
            return null;
        }
    }

    render() { 
        
        const Filler = (props) => {
            // Savoir faire
            const myArray = [this.state.moyenneCat1, this.state.moyenneCat2, this.state.moyenneCat3, this.state.moyenneCat4];
            myArray.sort((a, b) => b - a);
            const myNumber = myArray[0];
            // Savoir faire - détails
            const myArray2 = [this.props.acquis, this.props.enCours, this.props.nonAcquis, this.props.nonEvalue];
            myArray2.sort((a, b) => b - a);
            const myNumber2 = myArray2[0];
            // Savoir être
            const myArray3 = [this.props.knowtobecat1, this.props.knowtobecat2, this.props.knowtobecat3, this.props.knowtobecat4];
            myArray3.sort((a, b) => b - a);
            const myNumber3 = myArray3[0];

            let styleBck = '#1DA598';
            if(props.percentage === myNumber || props.percentage === myNumber2 || props.percentage === myNumber3) {
                styleBck = 'rgb(178, 128, 201)';
            }
            
            return (
                <>
                <div className='number'>{Math.round(props.percentage / props.maxValue * 100)}% - {Math.round(props.percentage/props.maxValue*30)}/{props.moyenneValue}</div>
                <div className='filler' style={{width: `${Math.round(props.percentage /props.maxValue * 100)}%`, background: `${styleBck}`}}/>
                </>
            )
        }

        const Progress = (props) => {
            return(
                <div className='progress-bar'>
                    <Filler percentage={props.percentage} maxValue={props.maxValue} moyenneValue={props.moyenneValue}/>
                </div>
            )
        }

        return ( 
            <>
            <div className='results'>
                <section className='App-results'>
                    <h3>Savoir faire - Catégories</h3>
                    <h4>Catégorie 1:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{`${Math.round(this.state.moyenneCat1/30*100)}%`} - {this.state.moyenneCat1}/{30}</div>
                        <div className='filler' style={{background: this.state.styleSF1, width: '100%'}}/>
                    </div>
                    <h4>Catégorie 2:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{`${Math.round(this.state.moyenneCat2/30*100)}%`} - {this.state.moyenneCat2}/{30}</div>
                        <div className='filler' style={{background: this.state.styleSF2, width: '100%'}}/>
                    </div>
                    <h4>Catégorie 3:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{`${Math.round(this.state.moyenneCat3/30*100)}%`} - {this.state.moyenneCat3}/{30}</div>
                        <div className='filler' style={{background: this.state.styleSF3, width: '100%'}}/>
                    </div>
                    <h4>Catégorie 4:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{`${Math.round(this.state.moyenneCat4/30*100)}%`} - {this.state.moyenneCat4}/{30}</div>
                        <div className='filler' style={{background: this.state.styleSF4, width: '100%'}}/>
                    </div>
                </section>
                <section className='App-results'>
                    <h3>Savoir faire - Détails</h3>
                    <h4>Acquis:</h4>
                    <Progress percentage={this.props.acquis} maxValue={120} moyenneValue={120}/>
                    <h4>En cours d'acquisition:</h4>
                    <Progress percentage={this.props.enCours} maxValue={120} moyenneValue={120}/>
                    <h4>Non acquis:</h4>
                    <Progress percentage={this.props.nonAcquis} maxValue={120} moyenneValue={120}/>
                    <h4>Non évalué:</h4>
                    <Progress percentage={this.props.nonEvalue} maxValue={120} moyenneValue={120}/>
                </section>
                <section className='App-results'>
                <h3>Savoir être</h3>
                    <h4>Catégorie 1:</h4>
                    <Progress percentage={this.props.knowtobecat1} maxValue={15} moyenneValue={15}/>
                    <h4>Catégorie 2:</h4>
                    <Progress percentage={this.props.knowtobecat2} maxValue={15} moyenneValue={15}/>
                    <h4>Catégorie 3:</h4>
                    <Progress percentage={this.props.knowtobecat3} maxValue={15} moyenneValue={15}/>
                    <h4>Catégorie 4:</h4>
                    <Progress percentage={this.props.knowtobecat4} maxValue={15} moyenneValue={15}/>
                </section>
                <section className='App-results'>
                <h3>Résultat final</h3>
                    <h4>Savoir faire:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{this.state.categoryHow}</div>
                        <div className='filler' style={{background: 'rgb(178, 128, 201)', width: '100%'}}/>
                    </div>
                    <h4>Savoir être:</h4>
                    <div className='progress-bar'>
                        <div className='number'>{this.state.categoryToBe}</div>
                        <div className='filler' style={{background: 'rgb(178, 128, 201)', width: '100%'}}/>
                    </div>
                    <h4>Retenue:</h4>
                    <div className='progress-bar'>
                        <div className='number'>Catégorie {this.state.catRetenue} / 4</div>
                        <div className='filler' style={{background: 'rgb(178, 128, 201)', width: '100%'}}/>
                    </div>
                    <h4>Palier:</h4>
                    <div className='progress-bar'>
                        <div className='number'>Palier {Math.round(9 / 100 * this.state.totalPercent)}</div>
                        <div className='filler' style={{background: 'rgb(178, 128, 201)', width: '100%'}}/>
                    </div>
                </section>
                
            </div>
            <button type='button' className='closeResults' onClick={this.props.closeResults}>Fermer</button>
            </>
         );
    }
}
 
export default Results;